<template>
  <div id="wroot" v-loading="loading">
    <div id="box">

      <p><strong></strong></p>
      <el-input v-model="inputWord" placeholder=""></el-input>
      <el-button type="primary" v-on:click="handleClick"></el-button>
    </div>
    <div id="body"/>
  </div>

</template>

<script>
import Fetch from "@/utils/Fetch";
import * as d3 from 'd3';
import {queryRelatedWord} from "@/api/api"
import { Message } from "element-ui";
export default {
  name: "WordConnect",
  data() {
    return {
      inputWord: "",
      relatedWord: [],
      circle: undefined,
      edges_line: undefined,
      edges_text: undefined,
      loading: false,
    }
  },
  mounted() {
    // this.drawRelatedGraph10()
  },
  methods: {
    handleClick() {
      this.loading = true
      this.queryRelatedWord10(this.inputWord).then(() => {
        this.drawRelatedGraph(this.inputWord)
        this.loading = false
      }).catch((reason) => {
        this.loading = false
        Message.error(reason)
        // alert(reason)
      })
    },
    queryRelatedWord10(word) {
      if (word === undefined) {
        alert("")
        return
      }
      let data = {
        "wordList": [word],
        "num": 10
      }
      return queryRelatedWord(data).then((res) => {
        this.relatedWord = res.data.data.data
      })
    },
    drawRelatedGraph(word) {
      let _this = this
      let idx = 0
      var links = this.relatedWord.map((item) => {
        idx++;
        return {
          source: word,
          target: item[0],
          type: idx,
          rela: item[1],
        }
      })

      // links = [
      //   {source:"",target:"",type:1,rela:0.23},
      //   {source:"",target:"",type:2,rela:0.45},
      //   {source:"",target:"",type:3,rela:0.67},
      //   {source:"",target:"",type:4,rela:0.23},
      //   {source:"",target:"",type:5,rela:0.74},
      //   {source:"",target:"",type:6,rela:0.28},
