              //   return color = "#C03939";
              // } else if (link.type === "3") {
              //   return color = "#5095FF";
              // } else if (link.source.name.length <= 4) {//
              //   return color = "#CD994C";
              // } else {
              //   return color = '#1943AC'
              // }
            }
