                return '';
              });

              d3.select(this).append('tspan')
                  .attr('x', 0)
                  .attr('y', -15)
                  .text(function () {
                    return top;
                  });

              d3.select(this).append('tspan')
                  .attr('x', 0)
                  .attr('y', 2)
                  .text(function () {
                    return bot;
                  });

              d3.select(this).append('tspan')
                  .attr('x', 0)
                  .attr('y', 16)
                  .text(function () {
                    return bot1;
                  });

            } else if (d.name.length > 12 && d.name.length <= 16) {//12 '

              var top = d.name.substring(0, 4);
              var bot = d.name.substring(4, 8);
              var bot1 = d.name.substring(8, 12);
              var bot2 = d.name.substring(12, d.name.length);

              d3.select(this).text(function () {
                return '';
              });

              d3.select(this).append('tspan')
                  .attr('x', 0)
                  .attr('y', -20)
                  .text(function () {
                    return top;
                  });

              d3.select(this).append('tspan')
                  .attr('x', 0)
                  .attr('y', -3)
                  .text(function () {
                    return bot;
                  });

              d3.select(this).append('tspan')
                  .attr('x', 0)
                  .attr('y', 10)
                  .text(function () {
                    return bot1;
                  });
              d3.select(this).append('tspan')
                  .attr('x', 0)
                  .attr('y', 23)
                  .text(function () {
                    return bot2;
                  });


            } else if (d.name.length > 16) {//16  
              var top = d.name.substring(0, 4);
              var bot = d.name.substring(4, 8);
              var bot1 = d.name.substring(8, 12);
              var bot2 = d.name.substring(12, 14);

              bot2 += '...'
              d3.select(this).text(function () {
                return '';
              });

              d3.select(this).append('tspan')
                  .attr('x', 0)
                  .attr('y', -22)
                  .text(function () {
                    return top;
                  });

              d3.select(this).append('tspan')
                  .attr('x', 0)
                  .attr('y', -7)
                  .text(function () {
                    return bot;
                  });

              d3.select(this).append('tspan')
                  .attr('x', 0)
                  .attr('y', 10)
                  .text(function () {
                    return bot1;
                  });
              d3.select(this).append('tspan')
                  .attr('x', 0)
                  .attr('y', 25)
                  .text(function () {
                    return bot2;
                  });
