import axios from "axios";
let baseUrl = "";

const service = axios.create({
    baseURL: baseUrl, // api  base_url
    timeout: 15000, // 
    withCredentials: true,
    headers: {
        "Content-Type": "application/x-www-form-urlencoded; charset=utf-8",
    },
});

// axios
export default (opts) => {
    const result = service(opts);
    return result;
};